
import { defineComponent } from 'vue'
import useBColController from '@/components/BCard/BCardController'

export default defineComponent({
  name: 'BCard',
  props: {
    tag: {
      type: String,
      default: 'div'
    }
  },
  setup(props, ctx) {
    const { imgTop, imgBottom } = useBColController(ctx)

    return {
      imgTop,
      imgBottom
    }
  }
})
