
import { defineComponent } from 'vue'
import useBCollapseController from '@/components/BCollapse/BCollapseController'

export default defineComponent({
  name: 'BCollapse',
  props: {
    id: {
      type: String
    },
    isNav: {
      type: Boolean,
      default: false
    }
  },

  setup(props) {
    const { isNavBar } = useBCollapseController(props.isNav)

    return { isNavBar }
  }
})
