
import { defineComponent, watchEffect, ref } from 'vue'
import useBOverlayController from '@/components/BOverlay/BOverlayController'

export default defineComponent({
  name: 'BOverlay',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    rounded: {
      type: String
    }
  },
  setup(props) {
    const { componentKey } = useBOverlayController(props)

    return {
      componentKey
    }
  }
})
