
import { defineComponent } from 'vue'
import useBIconController from '@/components/BIcon/BIconController'

export default defineComponent({
  name: 'BIcon',
  inheritAttrs: false,
  props: {
    icon: {
      type: String
    },
    variant: {
      type: String
    },
    fontScale: {
      type: Number
    }
  },
  setup(props) {
    const { fontScaleCalc, textVariant } = useBIconController(props)

    return {
      fontScaleCalc,
      textVariant
    }
  }
})
