
import { defineComponent, computed } from 'vue'
import useBFormGroupController from '@/components/BFormGroup/BFormGroupController'

export default defineComponent({
  name: 'BFormGroup',
  props: {
    label: {
      type: String
    },
    labelFor: {
      type: String
    },
    state: {
      type: Boolean,
      default: null
    },
    id: {
      type: String
    },
    validFeedback: {
      type: String
    },
    invalidFeedback: {
      type: String
    },
    description: {
      type: String
    }
  },
  setup(props) {
    const { stateValidation, ariaStateValidation } = useBFormGroupController(
      props
    )
    return {
      stateValidation,
      ariaStateValidation
    }
  }
})
