
import { defineComponent } from 'vue'
import { useBDropdownController } from '@/components/BDropdown/BDropdownController'

export default defineComponent({
  name: 'BDropdown',
  props: {
    id: {
      type: String
    },
    variant: {
      type: String,
      default: 'secondary'
    },
    right: {
      type: Boolean,
      default: false
    },
    dropup: {
      type: Boolean,
      default: false
    },
    dropright: {
      type: Boolean,
      default: false
    },
    dropleft: {
      type: Boolean,
      default: false
    },
    size: {
      type: String
    }
  },
  setup(props) {
    const {
      addedStyle,
      isDropup,
      isDropright,
      isDropleft,
      isRight
    } = useBDropdownController(props)

    return { addedStyle, isDropup, isDropright, isDropleft, isRight }
  }
})
